// Imports
@import url('https://fonts.googleapis.com/css2?family=Francois+One&display=swap');

// Variable
$background: #121322;
$control-bg: #1e1945;
$control-outline: white;
$control-color: rgba(255,255,255,0.75);

html {
  background: $background;
}

body {
  margin: 0;
}

.controls {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  width: 100vw;
  height: 100vh;
}

.control {
  width: 80%;
  text-align: center;
  margin: 10px;
  background: $control-bg;
  color: $control-color;
  border: solid $control-color 3px;
  font-family: 'Francois One', sans-serif;
  font-size: 400%;
  cursor: pointer;

  &:hover {
    // background: $control-outline;
    color: $control-bg;
    border-color: $control-bg;

    animation-name: flash-white;
    animation-duration: 3s;
  }

  &:active {
    transform: scale(0.9);
  }
}

@keyframes flash-white {
    0% { background: $control-bg; }
    1%  { background: $control-outline; }
    5%  { background: $control-outline; }
    6%  { background: $control-bg; }
}

// -- Util -- //
.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
